/* responsive fpr mobile */
@media only screen and (max-width: 600px) {
  /* quotation */

  .quotation .text {
    width: 100%;
  }
  .quotation .text h1 {
    font-size: 180px;
  }
  .quotation .text h2 {
    font-size: 50px;
  }

  /* How its works */
  .howitworks .div1 {
    display: none;
  }
  .howitworks .bottom {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    margin-top: 3rem;
  }

  /* EVENTS */
  .events .slider1 {
    display: grid;
    grid-template-columns: 1fr;
  }
}

